<template>
<div class="tier">
  <div id="tierParent" style="margin:auto" class="tierWidth">
    <div v-for="(tierData, index) in tierList" :key="index">
      <div id="test" :style="'position:relative; background-image:url(' + this.$root.IMG_PATH + 'bar_0' + tierData['color'] + '_' + this.$root.styleKbn + '.png); background-repeat:repeat-y; background-size:100% auto; width:100%; min-height:' + tierParentHeight + 'px'">
      <!-- <div id="test" :style="'position:relative; width:100%; min-height:110px'"> -->
        <!-- ティア名 -->
        <input type="text" :style="'background-color:transparent; border:none; position:absolute; left:10%; top:' + tierParentHeight / 2 + 'px; color:white; text-align:center; font-size:20px;' + this.$root.fScale('XY', 15)" placeholder="Tier Name" v-model="tierData['name']" maxlength="10">
        <!-- <span :style="'position:absolute; left:10%; top:' + tierParentHeight / 2 + 'px; color:white'" class="absoluteCenterXY">
          aiueo
        </span> -->
        <!-- 設定 -->
        <img :src="this.$root.IMG_PATH + 'btn_setting.png'" :style="'cursor:pointer; position:absolute; left:4%; top:' + tierParentHeight * 0.67 + 'px; height:' + tierParentHeight * 0.25 + 'px'" v-on:click="pushTierRowBtn('SETTING', index)">
        <!-- 削除 -->
        <img :src="this.$root.IMG_PATH + 'btn_delete_' + this.$root.styleKbn + '.png'" :style="'cursor:pointer; position:absolute; right:0%; top:6%; height:' + tierParentHeight * 0.2 + 'px'" v-on:click="pushTierRowBtn('DELETE', index)">
        <!-- 追加 -->
        <img v-show="index != 5 && this.tierList.length != 6" :src="this.$root.IMG_PATH + 'btn_plus_' + this.$root.styleKbn + '.png'" :style="'cursor:pointer; position:absolute; right:0%; bottom:6%; height:' + tierParentHeight * 0.2 + 'px'" v-on:click="pushTierRowBtn('ADD', index)">
         
        <!-- 中心部 -->
        <div id="box1" class="box" :style="'margin-left:20%; text-align:left; width:75%; min-height:' + tierParentHeight + 'px'">
          <draggable v-model="tierData['items']" item-key="no" group="ITEMS" :style="'height:100%; min-height:' + tierParentHeight + 'px'" @end="onEnd" @choose="touchStart($event)">
            <template #item="{ element }">
              <span :id="element.tierNo" :style="'display:inline-block; padding:' + tierParentHeight * 0.1 + 'px 0px; cursor:pointer; width:auto; height:auto'">
                <img v-if="element.type == 'IMAGE'" :src="element.path + this.$root.charaKbn + '.png'" :style="'height:' + tierParentHeight * 0.8 + 'px'">
              </span>
            </template>
          </draggable>
        </div>
      </div>
    </div>
    <div id="box2" class="box">
      <draggable v-model="baseItems" item-key="no" group="ITEMS" style="padding:0px; gap: 10px 10px" @end="onEnd"  @choose="touchStart($event)">
        <template #item="{ element }">
          <span :id="element.tierNo" :style="'display:inline-block; padding:' + tierParentHeight * 0.1 + 'px 0px; cursor:pointer; width:auto; height:auto'">
            <img v-if="element.type == 'IMAGE'" :src="element.path + this.$root.charaKbn + '.png'" :style="'height:' + tierParentHeight * 0.8 + 'px'">
          </span>
        </template>
      </draggable>
    </div>
  </div>
</div>
<div style="text-align:right">
  <img :src="this.$root.IMG_PATH + 'btn_reset_' + this.$root.styleKbn + '_on.png'" :style="'cursor:pointer; width:' + tierParentWidth * 0.08 + 'px'" v-on:click="pushBtn('RESET')">
</div>

<!-- スクショ用 -->
<div id="screen" :class="'bcStyle' + this.$root.styleKbn" :style="'padding:100px; position:absolute; left:-2100px; top:100px; width:' + (this.$root.screenWidth + 200) + 'px;'">
  <div style="position:relative; text-align:left">
    <img id="logoImg" :style="this.$root.logoMargin" :src="this.$root.IMG_PATH + 'gametitle_' + this.$root.styleKbn + '.png'" style="width:80%;">
    <img id="titleBar" :src="this.$root.IMG_PATH + 'title_bar.png'" style="position:absolute; width:80%; left:10%; top:53%;">
    <p style="color:white; position:absolute; text-align:center; left:0; top:64%; font-size:35px; width:100%">{{this.$root.title}}</p>
  </div>
  <div style="margin:auto">
    <div v-for="(tierData, index) in tierList" :key="index">
      <div id="test" :style="'position:relative; background-image:url(' + this.$root.IMG_PATH + 'bar_0' + tierData['color'] + '_' + this.$root.styleKbn + '.png); background-repeat:repeat-y; background-size:100% auto; width:100%; min-height:' + scTierParentHeight + 'px'">
      <!-- <div id="test" :style="'position:relative; width:100%; min-height:110px'"> -->
        <!-- ティア名 -->
        <p style="color:white; width:19%; font-size:25px; position:absolute; text-align:center; left:0px; top: 48px;">{{tierData["name"]}}</p>
        <!-- 中心部 -->
        <div id="box1" class="box" :style="'margin-left:20%; text-align:left; width:75%; min-height:' + scTierParentHeight + 'px'">
          <draggable v-model="tierData['items']" item-key="no" group="ITEMS" :style="'height:100%; min-height:' + scTierParentHeight + 'px'" @end="onEnd" @choose="touchStart($event)">
            <template #item="{ element }">
              <span :id="element.tierNo" :style="'display:inline-block; padding:' + scTierParentHeight * 0.1 + 'px 0px; cursor:pointer; width:auto; height:auto'">
                <img v-if="element.type == 'IMAGE'" :src="element.path + this.$root.charaKbn + '.png'" :style="'width:' + scTierParentHeight * 0.8 + 'px; height:' + scTierParentHeight * 0.8 + 'px'">
              </span>
            </template>
          </draggable>
        </div>
      </div>
    </div>
  </div>
</div>

<div id="overlay" v-show="isShowPopup" v-on:click="closeOverlay($event)">
  <div v-show="popupKbn == 'DELETE'" id="content" style="text-align:center;" class="confirmArea">
    <div style="position:relative">
      <img :src="this.$root.IMG_PATH + 'window_confirm.png'" style="width:100%">
      <img :src="this.$root.IMG_PATH + 'btn_confirm_yes.png'" style="position:absolute; left:15%; top: 70%; width:30%" class="link" v-on:click="pushPopupBtn('CONFIRM_YES')">
      <img :src="this.$root.IMG_PATH + 'btn_confirm_no.png'" style="position:absolute; right:15%; top: 70%; width:30%" class="link" v-on:click="pushPopupBtn('CONFIRM_NO')">
    </div>
  </div>
</div>
</template>

<script>
import draggable from 'vuedraggable'
// import axios from 'axios'

export default {
  name: 'Tier',
  components: {
    draggable
  },
  data() {
    return {
      scWidth:1000,
      
      barScale : 0.113,

      selSettingIndex : 0,
      isShowPopup : false,
      setName : "",
      setColorIndex : 0,

      title : "タイトル未設定",
      isEditTitle : false,

      tierColorList : [
        "#00b894", "#00cec9", "#0984e3", "#6c5ce7", "#fdcb6e", "#e17055", "#d63031", "#e84393"
      ],
      
      tierList : [
        {name:"SS", color:1, items:[]},
        {name:"S", color:2, items:[]},
        {name:"A", color:3, items:[]},
        {name:"B", color:4, items:[]},
        {name:"C", color:5, items:[]},
      ],
      baseItems: [],
      categoryData : {},

      isShowScreen : false,

      customText : "",

      hScale : 1.0,

      targetItem : null,

      tierNo : 0,
      isCustom : false,
      abc : "13px",

      popupKbn : "",
    };
  },
  computed : {
    tierParentWidth () {
      return this.$root.baseParentWidth;
    },
    tierParentHeight () {
      return this.tierParentWidth * this.barScale;
    },
    scTierParentHeight () {
      return this.$root.screenWidth * this.barScale;
    },
		selectedColor () {
      return (index) => {
        if (index == this.setColorIndex) {
          return "setSelectedColor";
        } else {
          return "";
        }
			}
    },
  },
  mounted () {
    // this.tierParentWidth = document.getElementById("tierParent").clientWidth;
    // window.addEventListener('resize', this.calculateWindowWidth);
  },
  beforeRouteLeave(to, from, next) {
    //イベントリスナー解除
    window.removeEventListener('resize', this.calculateWindowWidth);
    next();
  },
  methods : {
    pushTierRowBtn : function (kbn, index) {
      if (kbn == "SETTING") {
        //色変更
        let cl = this.tierList[index].color;
        cl++;
        if (cl == 7) {
          cl = 1;
        }
        this.tierList[index].color = cl;
      } else if (kbn == "DELETE") {
        this.popupKbn = "DELETE";
        this.isShowPopup = true;
        this.selSettingIndex = index;
      } else if (kbn == "ADD") {
        let addIndex = index + 1;
        
        //カテゴリを追加
        let after = this.tierList.splice(addIndex);
        Array.prototype.push.apply(this.tierList, [{name:"", color:1, items:[]}]);
        Array.prototype.push.apply(this.tierList, after);
      }
    },
    pushPopupBtn : function (name) {
      if (name == "CONFIRM_YES") {
        if (this.popupKbn == "DELETE") {
          //削除
          //まず中身を戻す
          let targetList = this.tierList[this.selSettingIndex].items;
          for (let i = 0; i < targetList.length; i++) {
            let data = targetList[i];
            this.baseItems.push(data);
          }
          //カテゴリーを削除
          this.tierList.splice(this.selSettingIndex, 1);
          this.isShowPopup = false;
        }
      } else if (name == "CONFIRM_NO") {
        this.isShowPopup = false;
      }
    },
    touchStart : function(e){
      console.log(e);
      let tTierNo = e.item.id;
      
      // console.log(tTierNo);
      for (let i = 0; i < this.tierList.length; i++) {
        const items = this.tierList[i].items;
        for (let j = 0; j < items.length; j++) {
          const element = items[j];

          if (tTierNo == element.tierNo) {
            this.targetItem = element;
            console.log(this.targetItem);
            return;
          }
        }
      }

      for (let i = 0; i < this.baseItems.length; i++) {
        if (tTierNo == this.baseItems[i].tierNo) {
          this.targetItem = this.baseItems[i];
          console.log(this.targetItem);
          return;
        }        
      }
    },
    onFileChange : function (e) {
      const files = e.target.files || e.dataTransfer.files;
      console.log(files);

      if (files.length == 0) {
        return;
      }

      for (let i = 0; i < files.length; i++) {
        const element = files[i];

        //形式
        if (element.type == "image/jpeg" || element.type == "image/png") {
          this.createImage(element);
        }
      }
    },
    createImage(file) {
      const reader = new FileReader();
      reader.onload = e => {
        //画像の縦横を取得
        let img = new Image();
        img.src = e.target.result;
        img.onload = function() {
          let width = img.naturalWidth;
          let height = img.naturalHeight;
          let scale = height / width;
          this.baseItems.push({path:e.target.result, type:"IMAGE", isCustom: true, scale:scale, tierNo:this.tierNo});
          this.tierNo++;
          // console.log(this.baseItems);
        }.bind(this);
      };
      reader.readAsDataURL(file);
    },
    calculateWindowWidth : function () {
      this.tierParentWidth = document.getElementById("tierParent").clientWidth;
    },
    show : function () {
      this.isShowScreen = !this.isShowScreen;
    },
    pushBtn : function(name) {
      if (name == "DELETE_ITEM") {
        //対象のID
        let tTierNo = this.targetItem.tierNo;
        console.log(tTierNo)
        for (let i = 0; i < this.tierList.length; i++) {
          let items = this.tierList[i]["items"];

          for (let j = 0; j < items.length; j++) {
            const element = items[j];
            console.log(j + "   " + element.tierNo);
            if (element.tierNo == tTierNo) {
              items.splice(j, 1);
              this.targetItem = null;
              return;
            }
          }
        }

        for (let i = 0; i < this.baseItems.length; i++) {
          const element = this.baseItems[i];
          console.log(i + "   " + element.tierNo);
          if (element.tierNo == tTierNo) {
            this.baseItems.splice(i, 1);
            this.targetItem = null;
            return;
          }
        }
      } else if (name == "BACK") {
        this.$router.push("/");
      } else if (name == "RESET") {
        //全てリセット
        for (let i = 0; i < this.tierList.length; i++) {
          let targetList = this.tierList[i].items;

          for (let j = 0; j < targetList.length; j++) {
            let data = targetList[j];
            this.baseItems.push(data);
          }
          this.tierList[i].items = [];
        }
        this.targetItem = null;
      }
    },
		closeOverlay : function (e) {
			if (e.target.id == "overlay") {
        // this.tierList[this.selSettingIndex].name = this.setName;
        // this.tierList[this.selSettingIndex].color = this.setColorIndex;
				this.isShowPopup = false;
			}
		},
    onEnd : function() {
      if (this.isCustom) {
        return;
      }

      //現在の配置を保存      
      // let saveArray = {title: this.title, tier:this.tierList, base:this.baseItems};
      // let json_text = JSON.stringify(saveArray);
      // localStorage.setItem("toritori_" + this.categoryData.category_id, json_text);
    },
    getOptions : function() {
      return this.options;
    },
    initAction : function () {
      this.$root.pageName = "tier";
      
      let title = localStorage.getItem("TITLE_TIER");
      if (title == null) {
        title = "";
      }
      this.$root.title = title;

      let getItems = [];
      for (let i = 0; i < this.$root.charaList.length; i++) {
        getItems.push({path:this.$root.IMG_PATH + 'chara/icon_' + this.$root.charaList[i] + '_', type:"IMAGE", isCustom: false, scale:0, tierNo:this.tierNo});
        this.tierNo++;
      }
      this.baseItems = getItems;
      this.title = "";
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
}
</script>

<style>
