<template>
<div class="alignment">
  <div id="chartParent" :style="'width:100%; height: ' + this.chartParentWidth *0.5 + 'px; position:relative'">
    <img :src="this.$root.IMG_PATH + 'bg_chart_' + this.$root.styleKbn + '.png'" style="position:absolute; top:0; left:0; width:100%">
    <input type="text" v-model="columnT" class="align-text" style="left:50%; top:4.5%;" placeholder="input text" :style="'width:15%; font-size:17px;' + this.$root.fScale('XY', 15)">
    <input type="text" v-model="columnL" class="align-text" style="left:7%; top:51.5%;" placeholder="input text" :style="'width:15%; font-size:17px;' + this.$root.fScale('XY', 15)">
    <input type="text" v-model="columnR" class="align-text" style="left:93%; top:51.5%;" placeholder="input text" :style="'width:15%; font-size:17px;' + this.$root.fScale('XY', 15)">
    <input type="text" v-model="columnB" class="align-text" style="left:50%; top:98%;" placeholder="input text" :style="'width:15%; font-size:17px;' + this.$root.fScale('XY', 15)">
  </div>
  <div style="height:10px" />
  <div id="baseParent" style="padding:10px; display:flex; flex-wrap:wrap">
    <img v-for="(item, index) in baseItems" :key="index"
      :style="'height: ' + itemHeight + 'px; z-index:1000; left:' + item.left + '; top:' + item.top + ''"
      :src="item.path + this.$root.charaKbn + '.png'" :id="item.tierNo" :class="itemClass(item)" @mousedown.prevent="touchStart($event)" @touchstart.prevent="touchStart($event)" @touchmove.prevent="touchMove($event)" @touchend.prevent="touchEnd($event)">
  </div>
  <div style="text-align:right">
    <img :src="this.$root.IMG_PATH + 'btn_remove_' + this.$root.styleKbn + '_on.png'" :style="'cursor:pointer; width:' + chartParentWidth * 0.08 + 'px'" v-on:click="pushEditBtn('BACK')">
    &nbsp;&nbsp;
    <img :src="this.$root.IMG_PATH + 'btn_reset_' + this.$root.styleKbn + '_on.png'" :style="'cursor:pointer; width:' + chartParentWidth * 0.08 + 'px'" v-on:click="pushEditBtn('BACK_ALL')">
  </div>
</div>
<!-- スクショ用 -->
<div id="screen" :class="'bcStyle' + this.$root.styleKbn" :style="'padding:100px; position:absolute; left:-2300px; top:100px; width:' + (this.$root.screenWidth + 200) + 'px;'">
  <div style="position:relative; text-align:left">
    <img id="logoImg" :style="this.$root.logoMargin" :src="this.$root.IMG_PATH + 'gametitle_' + this.$root.styleKbn + '.png'" style="width:80%;">
    <img id="titleBar" :src="this.$root.IMG_PATH + 'title_bar.png'" style="position:absolute; width:80%; left:10%; top:53%;">
    <p style="color:white; position:absolute; text-align:center; left:0; top:64%; font-size:35px; width:100%">{{this.$root.title}}</p>
  </div>
  <div :style="'width:100%; height: ' + this.$root.screenWidth * 0.5 + 'px; position:relative'">
    <img :src="this.$root.IMG_PATH + 'bg_chart_' + this.$root.styleKbn + '.png'" style="position:absolute; top:0; left:0; width:100%">
    <input type="text" v-model="columnT" style="position:absolute; color:white; z-index:1001; border:none; background-color:transparent; text-align:center; left:43.5%; top:3%;" placeholder="input text" :style="'width:13%; font-size:20px;'">
    <input type="text" v-model="columnL" style="position:absolute; color:white; z-index:1001; border:none; background-color:transparent; text-align:center; left:0.7%; top:49.7%;" placeholder="input text" :style="'width:13%; font-size:20px;'">
    <input type="text" v-model="columnR" style="position:absolute; color:white; z-index:1001; border:none; background-color:transparent; text-align:center; left:86.3%; top:49.7%;" placeholder="input text" :style="'width:13%; font-size:20px;'">
    <input type="text" v-model="columnB" style="position:absolute; color:white; z-index:1001; border:none; background-color:transparent; text-align:center; left:43.5%; top:96.6%;" placeholder="input text" :style="'width:13%; font-size:20px;'">
    <img v-for="(item, index) in baseItems" :key="index" :style="'height: ' + this.$root.screenWidth * itemScalePerWidth + 'px; position:absolute; display:' + ssDisp(item.isBase) + '; z-index:1000; left:' + item.left + '; top:' + item.top + ''" :src="item.path + this.$root.charaKbn + '.png'" :id="item.tierNo">
  </div>
  <!-- <div id="chartParent" :style="'width:100%; height: ' + this.scWidth * 0.5 + 'px; position:relative'">
    <img :src="this.$root.IMG_PATH + 'bg_chart_' + this.$root.styleKbn + '.png'" style="width:100%">
    <input type="text" v-model="columnT" class="align-text absoluteCenterXY" style="position:absolute; left:50%; top:4%;" placeholder="input text">
    <input type="text" v-model="columnL" class="align-text absoluteCenterXY" style="left:7%; top:51.5%;" placeholder="input text">
    <input type="text" v-model="columnR" class="align-text absoluteCenterXY" style="left:93%; top:51.5%;" placeholder="input text">
    <input type="text" v-model="columnB" class="align-text absoluteCenterXY" style="left:50%; top:98%;" placeholder="input text">
    <img v-for="(item, index) in baseItems" :key="index" :style="'width:' + scWidth * itemScalePerWidth + 'px; height: ' + scWidth * itemScalePerWidth + '; position:absolute; display:' + ssDisp(item.isBase) + '; z-index:1000; left:' + item.left + '; top:' + item.top + ''" :src="item.path" :id="item.tierNo"> -->
  <!-- </div> -->
</div>
</template>

<script>
// import axios from 'axios'

export default {
  name: 'Alignment',
  components: {
  },
  data() {
    return {
      x : 0,
      y : 0,
      isTouch : false,
      targetEl : null,
      targetItem : null,
      baseItems : [],
      baseTextItems : [],

      // chartParentWidth : 1000,

      customText : "",

      title : "タイトル未設定",
      isEditTitle : false,

      hScale : 1,
      isCustom : false,
      tierNo : 0,

      columnT : "",
      columnR : "",
      columnB : "",
      columnL : "",

      itemHeight : 100,
      itemScalePerWidth : 0.09,
      charaWHScale : 0.775,
      scWidth : 1200,

      limTop : 4.0,
      limBottom : 80.4,
      limLeft : 6.97,
      limRight : 84.1,
    };
  },
  computed : {
    chartParentWidth () {
      return this.$root.baseParentWidth;
    },
    ssDisp : function() {
      return (isBase) => {
        if (!isBase) {
          return "flex";
        } else {
          return "none";
        }
      }
    },
    itemClass : function () {
      return (item) => {
        let retVal = "unselectAItem ";
        if (this.targetItem != null && item.tierNo == this.targetItem.tierNo) {
          retVal = "selectItem ";
        }
        if (item.isBase) {
          return retVal + "itemBase";
        } else {
          return retVal + "itemChart";
        }
      }
    },
  },
  mounted () {
    // this.chartParentWidth = document.getElementById('chartParent').clientWidth;
    this.itemHeight = this.$root.baseParentWidth * this.itemScalePerWidth;
    window.addEventListener('resize', this.calculateWindowWidth);

    window.addEventListener('mousemove', this.touchMove);
    window.addEventListener('mouseup', this.touchEnd);
  },
  beforeRouteLeave(to, from, next) {
    //イベントリスナー解除
    window.removeEventListener('resize', this.calculateWindowWidth);
    window.removeEventListener('mousemove', this.touchMove);
    window.removeEventListener('mouseup', this.touchEnd);
    next();
  },
  methods : {
    calculateWindowWidth : function () {
      // this.chartParentWidth = document.getElementById('chartParent').clientWidth;
      // this.itemWidth = this.chartParentWidth * this.itemScalePerWidth;
      this.itemHeight = this.$root.baseParentWidth * this.itemScalePerWidth;

      // //右だけ再計算
      // let rightVal = this

    },
    pushEditBtn : function(name) {
      if (name == "BACK") {
        if (this.targetItem == null) {
          return;
        }

        document.getElementById("baseParent").append(this.targetEl)
        this.targetItem.isBase = true;
        this.targetEl = null;
        this.targetItem = null;
      } else if (name == "BACK_ALL") {
        let chartList = Array.from(document.getElementsByClassName("itemChart"));
        console.log(chartList.length);
        let baseParent = document.getElementById("baseParent");
        for (let i = 0; i < chartList.length; i++) {
          const element = chartList[i];
          baseParent.appendChild(element);
        }
        for (let i = 0; i < this.baseItems.length; i++) {
          this.baseItems[i].isBase = true;
        }
        this.targetEl = null;
        this.targetItem = null;
      }
    },
    mouseMove : function(e) {
      if (!this.isTouch) {
        return;
      }

      console.log(e);
    },
    mouseUp : function() {
      this.isTouch = false;
    },
    touchStart : function(e){
      // console.log(e);
      this.targetEl = e.target;
      // console.log(this.targetEl);
      
      var event = null;
      if(e.type == "mousedown") {
        event = e;
      } else if (e.type == "touchstart") {
        event = e.touches[0];
      }
      
      // console.log(event.pageX + " " + this.targetEl.offsetLeft);
      this.x = event.pageX - this.targetEl.offsetLeft;
      this.y = event.pageY - this.targetEl.offsetTop;

      let id = e.target.id;
      for (let i = 0; i < this.baseItems.length; i++) {
        if (id == this.baseItems[i].tierNo) {
          this.targetItem = this.baseItems[i];
          break;
        }        
      }
      for (let i = 0; i < this.baseTextItems.length; i++) {
        if (id == this.baseTextItems[i].tierNo) {
          this.targetItem = this.baseTextItems[i];
          break;
        }        
      }
      this.isTouch = true;
    },
    touchMove : function(e){
      if (!this.isTouch) {
        return;
      }
      if (this.targetItem.isBase) {
        //ベースから移行
        this.targetItem.isBase = false;
        document.getElementById("chartParent").appendChild(this.targetEl);
        var clientRect = document.getElementById("chartParent").getBoundingClientRect() ;
        var x = window.pageXOffset + clientRect.left;
        if (this.$root.charaKbn == "A") {
          this.x = x + this.itemHeight / 2;
        } else {
          this.x = x + this.itemHeight * this.charaWHScale / 2;
        }

        var y = window.pageYOffset + clientRect.top;
        this.y = y + this.itemHeight / 2;
        // console.log(this.targetEl.style.top + " " + this.targetEl.style.left);
        // console.log(e);
        // this.x = window.innerWidth / 2;
        // this.y = 75;
      }

      var event = null;
      if(e.type == "mousemove") {
        event = e;
      } else if (e.type == "touchmove") {
        event = e.touches[0];
      }
      let top = event.pageY - this.y;
      let left = event.pageX - this.x;

      let cWidth = document.getElementById('chartParent').clientWidth;
      let cHeight = document.getElementById('chartParent').clientHeight;

      // let tWidth = this.targetEl.clientWidth;
      // let tHeight = this.targetEl.clientHeight;
      // console.log(left + " " + cWidth + " " + tWidth + " " + (cWidth - tWidth));

      let topPer = top / cHeight * 100;
      let leftPer = left / cWidth * 100;
      console.log(leftPer + " " + topPer);
      
      let limRight = this.limRight;
      if (this.$root.charaKbn == "B") {
        limRight += 1.8;
      }

      if (topPer < this.limTop) {
        this.targetItem.top = this.limTop + "%";
      } else if (topPer > this.limBottom) {
        this.targetItem.top = this.limBottom + "%";
      } else {
        this.targetItem.top = topPer + "%";
      }

      if (leftPer < this.limLeft) {
        this.targetItem.left = this.limLeft + "%";
      } else if (leftPer > limRight) {
        this.targetItem.left = limRight + "%";
      } else {
        this.targetItem.left = leftPer + "%";
      }
    },
    touchEnd : function(){
      if (!this.isTouch) {
        return;
      }
      console.log("touchEnd");
      // console.log("touch end:%d,%d", e.offsetX, e.offsetY);
      this.isTouch = false;
    },
    touchLeave : function(){
      if (!this.isTouch) {
        return;
      }
      console.log("touchLeave");
      // console.log("touch leave:%d,%d", e.offsetX, e.offsetY);
      this.isTouch = false;
    },
    initAction : function() {
      console.log("A");
      let title = localStorage.getItem("TITLE_ALIGNMENT");
      if (title == null) {
        title = "";
      }
      this.$root.title = title;

      this.$root.pageName = "alignment";

      this.baseItems = [];
      for (let i = 0; i < this.$root.charaList.length; i++) {
        this.baseItems.push({tierNo:this.$root.charaList[i], path:this.$root.IMG_PATH + "chara/icon_" + this.$root.charaList[i] + '_', isCustom: false, scale:0, left:"0px", top:"0px", isBase:true,});        
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
}
</script>

<style>
.align-text {
  position:absolute;
  background-color:transparent;
  color : white;
  z-index:1001;
  border : none;
  text-align:center;
}



.itemChart {
  position : absolute;
}

.itemBase {
  position : static;
}

.chartWidth {
  width:95%;
}

.selectAItem {
  border : 2px solid red;
}
.unselectAItem {
  border : 2px solid transparent;
}


@media screen and (min-width : 1280px) {
  .chartWidth {
    width : 1200px;
  }
}
</style>