<template>
<div :style="'text-align:right; height:' + baseParentWidth * 0.04 + 'px'">
  <img v-show="this.styleKbn == 'A' && this.pageName == 'tier'" :src="this.IMG_PATH + 'btn_style1_A_on.png'" style="height:100%; cursor:pointer">
  <img v-show="this.styleKbn == 'B' || this.pageName == 'alignment'" :src="this.IMG_PATH + 'btn_style1_A_off.png'" style="height:100%; cursor:pointer" v-on:click="this.pushStyle('tier', 'A')">

  <img v-show="this.styleKbn == 'A' || this.pageName == 'alignment'"  :src="this.IMG_PATH + 'btn_style1_B_off.png'" style="height:100%; cursor:pointer" v-on:click="this.pushStyle('tier', 'B')">
  <img v-show="this.styleKbn == 'B' && this.pageName == 'tier'"  :src="this.IMG_PATH + 'btn_style1_B_on.png'" style="height:100%; cursor:pointer">

  <img v-show="this.styleKbn == 'A' && this.pageName == 'alignment'" :src="this.IMG_PATH + 'btn_style2_A_on.png'" style="height:100%; cursor:pointer">
  <img v-show="this.styleKbn == 'B' || this.pageName == 'tier'" :src="this.IMG_PATH + 'btn_style2_A_off.png'" style="height:100%; cursor:pointer" v-on:click="this.pushStyle('alignment', 'A')">

  <img v-show="this.styleKbn == 'A' || this.pageName == 'tier'"  :src="this.IMG_PATH + 'btn_style2_B_off.png'" style="height:100%; cursor:pointer" v-on:click="this.pushStyle('alignment', 'B')">
  <img v-show="this.styleKbn == 'B' && this.pageName == 'alignment'"  :src="this.IMG_PATH + 'btn_style2_B_on.png'" style="height:100%; cursor:pointer">

  <img v-show="this.charaKbn == 'A'" :src="this.IMG_PATH + 'btn_chara_A.png'" style="height:100%; cursor:pointer; margin-left:2%" v-on:click="this.pushChara('B')">
  <img v-show="this.charaKbn == 'B'" :src="this.IMG_PATH + 'btn_chara_B.png'" style="height:100%; cursor:pointer; margin-left:2%" v-on:click="this.pushChara('A')">

</div> 
<div style="position:relative; text-align:left">
  <img id="logoImg" :style="logoMargin" :src="this.IMG_PATH + 'gametitle_' + this.styleKbn + '.png'" style="width:80%;">
  <img id="titleBar" :src="this.IMG_PATH + 'title_bar.png'" style="position:absolute; width:80%; left:50%; top:80%" class="absoluteCenterXY">
  <input id="inputTitle" type="text" :style="'color:white !important; position:absolute; width:80%; text-align:center; background-color:transparent; border:none; vertical-align:middle; left:50%; top:80.5%; font-size:30px; height:40px; width:50%; ' + this.fScale('XY', 50)" placeholder="ここをクリックしてタイトルを編集" maxlength="40" v-model="title">
  <!-- <img :src="this.IMG_PATH + 'btn_setting.png'" :style="'position:absolute; right:0; top:80%; cursor:pointer; height:' + baseParentWidth * 0.04 + 'px;'" v-on:click="pushTitleChange" class="absoluteCenterY"> -->
</div>

<div id="rootBaseParent">
  <router-view/>
</div>

<div>
  <img :src="this.IMG_PATH + 'line_' + this.styleKbn + '.png'" style="width:100%">
  <div style="text-align:center">
    <img :src="this.IMG_PATH + 'btn_save_on.png'" :style="'cursor:pointer; width:' + baseParentWidth * 0.3 + 'px'" v-on:click="pushBtn('SAVE_SCREEN')">
    <img :src="this.IMG_PATH + 'icon_sankaku_' + this.styleKbn + '.png'" :style="'width:' + baseParentWidth * 0.03 + 'px; margin:0px ' + baseParentWidth * 0.05 + 'px'">
    <img :src="this.IMG_PATH + 'btn_share_on.png'" :style="'cursor:pointer; width:' + baseParentWidth * 0.3 + 'px'" v-on:click="pushBtn('SHARE')">
  </div>
  <p :style="'font-size:16px; transform: scale(' + (this.fontScale) + ')'">※画像を添付したい場合は、一度保存して、シェアボタンから画像を添付してください。</p>
</div>
<br>
<br>
<div style="text-align:right">
  <button type="button" class="btnBase" v-on:click="pushFriend">対戦相手を募集</button>
</div>
</template>

<script>
  export default {
    data() {
      return {
        IMG_PATH : "/sf6/app/img/",
        ICON_PATH : "/sf6/storage/icon/",
        CARD_PATH : "/sf6/storage/card/",

        SERVER_PATH : "http://localhost:8002/sf6/api/",
        ROOT_PATH : "/sf6/",
        DOMAIN_PATH : "localhost",
        
        // SERVER_PATH : "https://tarolab.flop.jp/sf6/api/", 
        // ROOT_PATH : "/sf6/",
        // DOMAIN_PATH : "tarolab.flop.jp",
        
        pageName : "",
        styleKbn : "A",
        charaKbn : "A",
        reloadValue : "", //画像の更新用,

        charaList : [
          "01",
          "02",
          "03",
          "04",
          "05",
          "06",
          "07",
          "08",
        ],

        baseParentWidth : 1000,
        title : "",
        fontScale : 1,

        screenWidth : 1200,
      }
    },
    created () {
      let style = localStorage.getItem("STYLE");
      if (style == null) {
        style = "A";
        localStorage.setItem("STYLE", "A");
      }
      this.styleKbn = style;
      if (this.styleKbn == "A") {
        document.body.className = "bcStyleA";
      } else {
        document.body.className = "bcStyleB";
      }

      let chara = localStorage.getItem("CHARA");
      if (chara == null) {
        chara = "A";
        localStorage.setItem("CHARA", chara);
      }
      this.charaKbn = chara;
    },
    mounted () {
      this.baseParentWidth = document.getElementById('rootBaseParent').clientWidth;
      this.fontScale = this.baseParentWidth / 1000;
      console.log(this.fontScale);
      window.addEventListener('resize', this.calculateWindowWidth);
    },
    computed : {
      fScale () {
        return (translate, baseWidth) => {
          let tStr = "";
          if (translate == "X") {
            tStr = "translateX(-50%)"
          } else if (translate == "Y") {
            tStr = "translateY(-50%)"
          } else if (translate == "XY") {
            tStr = "translate(-50%, -50%)";
          }
          
          let wStr = "";
          if (baseWidth != 0) {
            wStr = "; width:" + (baseWidth * (1/this.fontScale)) + "%";
          }

          console.log("transform: " + tStr + " scale(" + this.fontScale + "); -webkit-transform: " + tStr + " scale(" + this.fontScale + "); -ms-transform: " + tStr + " scale(" + this.fontScale) + ")" + wStr;
          return "transform: " + tStr + " scale(" + this.fontScale + "); -webkit-transform: " + tStr + " scale(" + this.fontScale + "); -ms-transform: " + tStr + " scale(" + this.fontScale + ")" + wStr;
        }

      },
      logoMargin () {
        if (this.$root.styleKbn == "A") {
          return "";
        } else {
          return "margin-left:10%";
        }
      },
      getHeight () {
        return (elName, scale) => {
          // console.log(elName + " " + scale);
          let el = document.getElementById(elName);
          if (el != null) {
            console.log(el.clientHeight);
            return el.clientHeight * scale + "px";
          }
          return "10px";
        }
      },
    },
    methods : {
      pushFriend : function () {
        window.open("https://tarolab.flop.jp/gamefriend/post/1090?page=1");
      },
      pushTitleChange : function() {
        document.getElementById("inputTitle").focus();
      },
      calculateWindowWidth : function () {
        this.baseParentWidth = document.getElementById("rootBaseParent").clientWidth;
        this.fontScale = this.baseParentWidth / 1000;
        console.log(this.fontScale);
      },
      pushStyle : function (kbn, style) {
        this.styleKbn = style;
        localStorage.setItem("STYLE", style);
        if (this.styleKbn == "A") {
          document.body.className = "bcStyleA";
        } else {
          document.body.className = "bcStyleB";
        }

        if (kbn != '') {
          //現在のタイトルを保存
          if (kbn == "tier") {
            localStorage.setItem("TITLE_ALIGNMENT", this.title);
          } else {
            localStorage.setItem("TITLE_TIER", this.title);
          }
          this.$router.push('/' + kbn);
        }
      },
      pushChara : function (kbn) {
        this.charaKbn = kbn;
        localStorage.setItem("CHARA", kbn);
      },
      pushBtn : function (name) {
        if (name == "SAVE_SCREEN") {
          //画像化
          window.html2canvas(document.getElementById('screen'), { 
            scale:1
          }).then(canvas => {
            console.log(canvas.width + " " + canvas.height);
            // var base64 = canvas.toDataURL('image/jpeg');
            // base64 = base64.replace(/^.*,/, '');

            const a = document.createElement("a");
            a.href = canvas.toDataURL("image/jpeg", 1);
            a.download = "image.jpg";
            a.click();

            // axios.post(this.$root.SERVER_PATH + "updateChallenge", params);      
          });
          this.targetItem = null;
        } else if (name == "SHARE") {
          let bodyStr = "【とりとりメーカー】" + this.title;
          let siteUrl = "http://www.yahoo.co.jp";
          var url = "https://twitter.com/intent/tweet?" + 
          "hashtags=" + encodeURIComponent('toritori') + 
          "&text=" + encodeURIComponent(bodyStr) + 
          "&url=" + encodeURIComponent(siteUrl);
          // "&via=" + encodeURIComponent("iphonetaro_live");
          window.open(url);
          this.targetItem = null;
        }
      }
    }
  }
</script>



<style>
#app {
  font-family: Meiryo, BIZ UDPGothic, UD Digi Kyokasho NK-B, Hiragino Maru Gothic Pro, Hiragino Maru Gothic ProN, Arial, sans-serif;
